import '../../Css/index.css';
import React from 'react';
import Page from '../../Page/PageElements/article.jsx';

import CompatibilityMenu from './menu';


const paragraphs = [
    <div>
        Jest to sekcja w ktrej umieszczone zostay wszelkie porady zwizane z konserwacj zarwno czci rowerowych, jak i 
        wszystkich innych rzeczy okoorowerowych. Znajdziesz wic tutaj tutoriale o tym, jak prawdidowo wyczyci rower i 
        przesmarowa acuch, ale rwnie zadba o odzie treningow, oraz zrobi prawidowo rozgrzewk. 
    </div>,
    <div>
        Wszystkie materiay zostay uszeregowane w odpowiednich, tematycznych dziaach.
    </div>,
    <CompatibilityMenu/>
]

export default class Compatibility extends React.Component {
    render() {
        return (
            <div>
                <Page
                    title="Praktyczne porady jak utrzyma rower"
                    secondaryTitle="w zdrowiu i czystoci"
                    paragraphs={paragraphs}
                    isBackButton={false}
                />
            </div>
        );
    }
}