import '../../../Css/index.css';
import React from 'react';

import Article from '../../../Page/PageElements/article.jsx';
import PartialService from './headset-partial.service';
import FullService from './headset-full.service';
import SubPage from '../../../Page/PageElements/section.jsx';
import SectionPanel from '../../../Page/ContentComponents/sectionPanel';


const introduction = [
    <div>Typowy niedzielny rowerzysta wie o sterach w swoim rowerze dokadnie tyle, ile problemu mu sprawiaj.
    Jest to cz (a raczej zestaw czci), ktre w porwnaniu do reszty roweru s mao awaryjna, w zwizku z czym nasza wiedza o nich sprowadza si do tego, e s.
    Co jednak w przypadku, kiedy po kilku latach uytkowania (lub jednej sesji z myjk cinieniow),
    kierownica nie krci si ju bez oporu, a okazyjnie wydaje z siebie podejrzane kliknita i zgrzyty? Wtedy mamy dwie opcje:</div>,
    <ul>
        <li>Wymiana oysk sterw</li>
        <li>Wymiana sterw w caoci</li>
    </ul>,
    <div>Jeli nie jeste mechanikiem i nie masz do dyspozycji specjalistycznych narzdzi, zdecydowanie odradza si opcj drug.
    Bez odpowiedniego przygotowania technicznego w najgorszym wypadku uszkodzisz konstrukcj sterw lub samej ramy. Ponadto w kadym wypadku
        polecam zapozna si z artkuem dotyczcym typw sterw oraz istniejcych standardw mocowania. <a href="/compatibility/headset/1">Stery</a>
    </div>
]

const paragraphs = [
    <SubPage
        title="Wymiana sterw"
        paragraphs={introduction}
    />,
    <SectionPanel
        title="Serwis czciowy"
        content={<PartialService />}
    />,
    <SectionPanel
        title="Peen serwis"
        content={<FullService />}
    />
]

export default class ForkReplaceArticle extends React.Component {
    render() {
        return (
            <div>
                <Article
                    title="Wymiana sterw"
                    paragraphs={paragraphs}
                    backButtonLink='/service'
                />
            </div>
        );
    }
}