import '../../../Css/index.css';
import React from 'react';
import ArticleBody from '../../../Page/PageElements/article.jsx';
import Section from '../../../Page/PageElements/section.jsx';
import { Link } from "react-router-dom";

const section1 = [
    <div>
        Uwaga, artyku tego zawiera informacje przydatne przy regulacji zawieszenia, <b>ktre nadaje si do regulacji</b>. W celu identyfikacji tego, 
        czy posiadasz takowe, zapoznaj si z artykuem o <Link to={'/compatibility/suspension/fork-types'}>amortyzatorach</Link>. W skrcie regulowa daj si
        amortyzatory powietrzne, natomiast najtasze, wyposaone jedynie w amortyzacj sprynow, z zasady adnej regulacji nie posiadaj.
    </div>,
    <div>
        Aby w ogle zacz, potrzebowa bdziemy <b>pompki do amortyzatorw</b>. Uprzedzajc twoje pytanie - nie, zwyka pompka rowerowa, ani kompresor si do tego nie nadaj.
        Regulacja amortyzatora wyglda niemal dokadnie tak samo jak regulacja dampera, a konkretne rnice zostan wyranie zaznaczone w tekcie. Nie ma te znaczenia jakiej marki
        masz amortyzator - wszystkie reguluje si w ten sam sposb. Dodatkowo potrzebna nam bdzie znajomo kilku podstawowych poj - SAG, tumienie oraz kompresja.
    </div>
]

const section2 = [
    <div>
        SAG to w duym skrcie stopie ugicia amortyzacji pod twoim ciarem. Zwyczajowo okrela si go procentowo, w zwizku z czym przydatna okae si tutaj miara.
        W chwili obecnej spora cz amortyzatorw marki Rock Shox ma tak miark nadrukowan na goleniach, wic jeli jeste wacicielem takowego masz na starcie nieco z grki.
        W przypadku wszystkich innych amortyzatorw zmierz dugo goleni. Jest to tzw. <b>skok amortyzatora</b>. Moesz te sprawdzi w specyfikacji roweru. 
        W przypadku przytaczajcej wikszoci rowerw grskich bdzie to 100 mm.
    </div>,
    <div>
        <b>UWAGA: jeli amortyzator jest zbyt sabo napompowany, to golenie bd mocno siedziay w rodku, przez co wynik bdzie niedokadny.</b>
    </div>,
    <div>
        Nastpnie wejd na rower (w warunkach domowych najlepiej jeli kto ci go przytrzyma) i usid na nim w najzwyklejszy moliwy sposb. Warto SAGu to nic innego
        jak stopie w ktrym golenie weszy w rodek amortyzatora. No dobrze, ale teraz jak to zmierzy? Jeeli twj amortyzator ma o-ring, czyli gumk umieszczon na goleni, umie
        j na samym dole, aby dotykaa uszczelek. Po zejciu z roweru amortyzator wrci do swojej normalnej pozycji, a o-ring znajdzie si gdzie po rodku, wskazujc jak mocno
        ugi si pod tob. Jeeli masz amortyzator bez o-ringu, musisz to niestety zmierzyc na oko samemu, np. zaznaczajc ten punkty pisakiem. 
    </div>,
    <div>
        Czyli jaki procent sagu jest prawidowy? Bezpiecznie jest przyj 20% na przd oraz 25-30% na ty. Jeeli jedzisz szybko, prawdopodobnie chcesz obniy te wartoci, natomiast
        do jazdy wyczynowej z czstymi skokami, SAG moe by odrobin wikszy.
    </div>
]

const section3 = [
    <div>
        W budowie.
    </div>
]

const section4 = [
    <div>
        W budowie.
    </div>
]

const content = [
    <Section paragraphs={section1} />,
    <Section paragraphs={section2} title={'SAG'} />,
    <Section paragraphs={section3} title={'Tumienie'} />,
    <Section paragraphs={section4} title={'Kompresja'} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Ustawienie cinienia amortyzacji"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}