import '../../../Css/index.css';
import React from 'react';

import SubPage from '../../../Page/PageElements/section.jsx';
import ChallengeBox from '../../../Page/ContentComponents/challengeBox.jsx';

const list = [
    <div>Zestaw kluczy szecioktnych</div>,
    <div>Smar montaowy</div>,
    <div>Szmatka</div>,
    <div>Motek z gumow gwk</div>
]

const paragraphs = [
<h1>Wymiana oysk sterw:</h1>,
    <ChallengeBox
    skillLevel = "challengeBar50"
    toolLevel = "challengeBar25"
    list = {list}
    />,
    <div>Aby dobra si do oysk bdziesz musia docelowo wyj widelec. 
        Bd na to przygotowany, aby w trakcie demontau nie zorientowa si, e pltanina napitych kabli ci to uniemoliwi, lub w znaczcy sposb utrudni, wyduajc ca napraw. 
        Prawdopodobnie dobrym rozwizaniem jest te przygotowanie podprki, aby ram bez widelca oprze w bezpieczny i stabilny sposb. 
        Sugeruj take wyjcie koa dla uatwienia sobie manewrw wyjtymi komponentami.</div>,
    <div>
        <ul>
            <li>Poluzuj ruby boczne wspornika kierownicy (mostek)</li>
            <li>Odkr zupenie rub grn rury sterowej i wyjmij j razem z kapslem,</li>
            <li>Zdejmij wspornik kierownicy z rury sterowej</li>
            <li>Zabezpiecz kierownic w taki sposb, aby nie luno wiszc na kablach nie porysowaa ramy</li>
            <li>Zdejmij wszystkie podkadki z rury sterowej oraz grne podkadki sterw,</li>
            <li>Jeli widelec nie wysuwa si z ramy i sterw, uyj motka z gumow gwk, lub innego podobnego narzdzia aby go wybi.</li>
                            <li>Zabezpiecz Widelec w taki sposb, aby zwisajc na kablach nie porysowa ramy</li>
            <li>Wyjmij oyska (s to albo wianki albo oyska maszynowe),</li>
            <li>Wyczy dokadnie miski sterw oraz same oyska szmatk do czysta. 
                a.	UWAGA: moe si zdarzy, e oyska s w dobrej kondycji i krc si bez oporu. Dokadnie wyczyszczone mog by uywane dalej. 
                W przypadku wiankw, ktrych ywotno jest duo nisza odradza si jednak ich ponowne zastosowanie.
                b.	Jeli wci nie masz pojcia z jakiego typu oysk korzysta twj rower, moesz przeczyta oznacza na ciance oyska maszynowego, 
                lub wymierzy je suwmiark</li>
            <li>Na warstw smaru na miski, umie w nich oyska, a nastpnie umie jeszcze minimaln ilo smaru na grnej stronie samych oysk,
                a.	Przy oyskach maszynowych zaleca si naoenie naprawd symbolicznej iloci smarw. Przy wiankowych ilo ta powinna by na tyle dua, 
                aby osadzenie wianka w dolnej misce uniemoliwiao jego wypadnicie.</li>
            <li>Na widelec, nakadki sterw, dystanse oraz mostek,</li>
            <li>Na kapsel, dokr grn rub</li>
            <li>Wyprostuj mostek, dokr ruby boczne mostka</li>
        </ul>
    </div>,
    ]

    const PartialService = () => {
        return (
            <div>
            <SubPage 
                title="Wymiana sterw"
                paragraphs={paragraphs}
            />
            </div>
        );
    }
     
    export default PartialService;