import '../../../Css/index.css';
import React from 'react';
import ArticleBody from '../../../Page/PageElements/article.jsx';
import TableTyreDimensions from '../Component/table-tyre-to-rim'


const paragraphs = [
    <div>
    W tym artykule kompletnie pomijam aspekt zwizany z bienikiem, sensem instalowania pewnego rodzaju opon w rnych typach roweru, oraz jakie s najlepsze modele i marki.
    Na warsztat wemiemy zatem wycznie aspekt techniczny, czyli jaki jest dopuszczalny i optymalny rozmiar opony w stosunku do obrczy i ramy. 
    Dla wszystkich, ktrzy s zieloni w kwestiach obowizujcych na rynku oznaczeniach szerokoci pozostawiam ponisze tabelki.
    </div>,
    <TableTyreDimensions/>, 
    <div>
      W stosunku do tego samego rozmiaru moemy spotka si z trzyma rnymi oznaczeniami. 
      Bdzie to system ISO, ktry podaje rednic obrczy w milimetrach np. 622. 
      Dalej mamy system cali, bdcy rednic caego koa wraz z opon np. 28. 
      I wreszcie system francuski, ktry jest zewntrzn rednic koa razem z balonem w mm. 
      Poniej kilka przykadw:
    </div>,
    <div>
      Wiemy ju zatem jak odczyta rozmiar opony, w aspekcie jej wysokoci lub rednicy. Co wic z szerokoci? 
      Jest ona oznaczana zwyczajowo w milimetrach do ok. 42 lub w calach powyej tej wartoci, a wic od ok. 1,6 cala. 
      Bardzo czsto podane s wartoci w obu miarach, aczkolwiek w praktyce przyjo si uywa tych konkretnych nazw w odniesieniu do wymienionych przedziaw. 
      Z czego to wynika? Opony wskie, a wic szosowe, zostay wymylone w Europie, natomiast szerokie, czyli rowery grskie w stanach zjednoczonych. 
    </div>,
    <div>
      Wracajc jednak to waciwego tematu, jeeli potrafimy ju odczyta wysoko oraz szeroko opony, pozostaje nam tylko sprawdzi, czy damy rad j umieci w rowerze.  
      Wysoko jest dopasowa atwo  istnieje jedna waciwa dla danej obrczy i nie mamy tutaj pola do manewru. 
      Jeeli wic mamy obrcz 622, to moemy zaoy na ni opon wycznie o tym samym wymiarze. 
      Nie ma moliwoci nacignicia gumy tak, aby wpasowaa si w wikszy typ obrczy, ani skurczenia jej dla mniejszych wersji.
    </div>,
    <div>
      Inn kwesti jest szeroko  na jedn obrcz wejdzie wiele opon o znaczco rnych od siebie szerokociach. 
      S jednak pewne limity, a mianowicie dostpna przestrze widelca oraz ramy. 
      Te maj bardzo rzadko podany maksymalny rozmiar opony w swojej specyfikacji, ale na pomoc przychodzi nam tutaj suwmiarka. 
      Pamitaj, e szeroko opony podana przez producenta, to estymowana szeroko w najszerszym jej miejscu, przy maksymalnym cinieniu. 
      Majc te informacj, moe mniej wicej oceni ten punkty na widekach tylnych swojej ramy. 
      Zmierz szeroko suwmiark, zachowujc przy tym bezpieczny margines  nie chcesz przecie, aby okazao si, e opona bdzie si blokowa lub trze o ram. 
      Podobnie da si wymierzy amortyzator lub widelec sztywny. 
    </div>,
    <div>
      To jednak nie koniec. W powyszy sposb jestemy w stanie oceni maksymalny rozmiar opony, ktry wejdzie do ramy lub widelca. 
      Pytanie czy kada szeroko jest dobra? Czy s szerokoci optymalne? Owszem s, ale tutaj bdziemy potrzebowali kolejn informacj, a wic wewntrzn szeroko twojej obrczy. 
      Jeeli nie chcesz zdejmowa opony, aby to sprawdzi, moesz zmierzy szeroko zewntrzn, a nastpnie odj ok. 3-4 mm. 
      Pamitaj jednak, e kada obrcz jest rna i takie podejcie zawsze jest obarczone ryzykiem. 
      Dodatkowo, jeeli masz markowe koa z dobrym wsparciem technicznym, prawdopodobnie w internecie znajdziesz ich pen specyfikacj  
      wraz z szerokoci zewntrzn i wewntrzn obrczy.
    </div>,
    <div>
      Jeeli ju dokonae pomiaru, lub zdobye t informacj w jakikolwiek inny sposb, moesz sprawdzi w tabeli powyej jakie s optymalne rozmiary opon dla twojej obrczy.
    </div>,
    <div>
      A teraz kilka zda wyjanienia dlaczego wanie te rozmiary s tymi najlepszymi. 
      Ot opona nie ma dokadnie takiego samego ksztatu na kadej obrczy. 
      Czym szersza jest obrcz w stosunku do opony, tym balon bdzie bardziej spaszczony, a, przekraczajc w kocu pewn granic, bdzie stawia dodatkowe opory toczenia. 
      Z drugiej strony zbyt wska obrcz moe powodowa, e opona nie bdzie miaa dostatecznego oparcia i w pewnym momencie wylizgnie si z rantw. 
      Stosujc podane powyej wartoci przy wyborze zarwno opony jak i obrczy, bdziesz mia pewno, e balon ma prawidowy ksztat, a sama opona stawia minimalne moliwe opory. 
    </div>
]

export default class Article extends React.Component {
  render() {
      return (
          <div>
              <ArticleBody
                  title="Jak dobra rozmiar opony do obrczychy"
                  paragraphs={paragraphs}
                  backButtonLink='/compatibility'
              />
          </div>
      );
  }
}