import '../../../Css/index.css';
import React from 'react';
import ArticleBody from '../../../Page/PageElements/article.jsx';
import ContentTile from '../../../Page/ContentComponents/contentTile';

import TableCassetteDimensions from '../Component/table-cassette-shimano-dimensions'

import ImageShimanoFreeWheel from '../../../../resources/wheel/freewheelTypes/ShimanoFreewheel.jpeg';
import ImageCampagnoloFreeWheel from '../../../../resources/wheel/freewheelTypes/CampagnoloFreewheel.jpeg';
import ImageShimanoSplineFreeWheel from '../../../../resources/wheel/freewheelTypes/ShimanoSplineFreewheel.jpeg';
import ImageSramFreeWheel from '../../../../resources/wheel/freewheelTypes/SramFreewheel.jpg';

const paragraphs = [
    <div>
    Kaseta to element kluczowy dla kadego napdu, w najwikszej mierze wpywajc na dostpne przeoenia. 
    Jednoczenie nie sposb jest omwi temat kaset, nie poruszajc tematu istniejcych standardw bbenkw piast.
    Na rynku najpopularniejszym standardem jest oczywicie klasyczny typ shimano. W zasadzie w 90% przypadkw rower bdzie mia wanie taki typ.
    W zwizku z coraz wyraniejsz ekspansj SRAM, zwaszcza za spraw napdw typu Eagle, w ostatnich latach coraz atwiej mona spotka take bbenki tej firmy.
    Wreszcie ostatnim typem, spotykanym bardzo rzadko i wycznie w rowerach z grnej pki, s bbenki Campagnolo.
    </div>,
    <TableCassetteDimensions/>, 
    <div><h2>Typy bbenkw</h2></div>,
    <ContentTile
    imageSource = {ImageShimanoFreeWheel}
    name = "Shimano"
    content = {<div>
      typ shimano bbenka
      </div>}
    />,
    <ContentTile
    imageSource = {ImageSramFreeWheel}
    name = "Sram"
    content = {<div>
      typ sram bbenka
      </div>}
    />,
    <ContentTile
    imageSource = {ImageCampagnoloFreeWheel}
    name = "Campagnolo"
    content = {<div>
      typ campagnolo bbenka
      </div>}
    />,
    <ContentTile
    imageSource = {ImageShimanoSplineFreeWheel}
    name = "Shimano Spline"
    content = {<div>
      typ shimano spline bbenka
      </div>}
    />,
    <div>
    </div>
]

export default class Article extends React.Component {
  render() {
      return (
          <div>
              <ArticleBody
                  title="Kaseta"
                  paragraphs={paragraphs}
                  backButtonLink='/compatibility'
              />
          </div>
      );
  }
}