import '../../../Css/index.css';
import React from 'react';
import Section from '../../../Page/PageElements/section.jsx';
import ArticleBody from '../../../Page/PageElements/article.jsx';

const section1 = [
    <div>
        Sprawdzenie, czy rower ma luzy to zgodnie z kad ksieczk gwarancyjn czynno, ktr powiniene obowizkowo wykonywa przed kad jazd. Moe a tak
        czsto nie ma potrzeby, ale powinna to by dla ciebie swego rodzaju rutyna, powtarzana regularnie w cigu sezonu. Tylko w ten sposb bdziesz mie pewno, e
        nie jedzisz na tykajcej bombie. 
    </div>
]

const section2 = [
    <div>
        Nacinij jedn rk przedni hamulec, a drug po na dolnej misce sterw. Nastpniej poruszaj rowerem w przd i w ty - jeeli masz luz na sterach, widelcu lub mostku,
        wyczujesz, e widelec i rama nie s "zczone". 
    </div>
]

const section3 = [
    <div>
        Stojc z boku roweru, chwy jedn rk mostek, a drug przednie koo i poruszaj nim w przd i w ty. Jeeli porusza si wzgldem mostka, to ma luzy na osi piasty.
        Z tylnem koem postpuj tak samo, ale zamiast mostka, chwy siodeko.
    </div>
]

const content = [
    <Section paragraphs={section1} />,
    <Section paragraphs={section2} title={'Stery'} />,
    <Section paragraphs={section3} title={'Koa'} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Sprawdzenie, czy rower ma luzy"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}