import '../../Css/index.css';
import React from 'react';
import Page from '../../Page/PageElements/article.jsx';


const paragraphs = [
    <div>
        Osoby tworzce t stron, to wieloletni zapalecy, ktrzy zby zjedli na naprawach zarwno tych najdroszych , jak i najtaszych i najbardziej
        budetowych rowerw. Jeli masz jakie sugestie odnonie tego, jak dziaa ta strona, uwagi odnonie prezentowanych tutaj artykuw, 
        lub po prostu chciaby podzieli si swoj opini, uderzaj na email <b>bike.workshop.info@gmail.com</b> . 
    </div>
]

export default class Compatibility extends React.Component {
    render() {
        return (
            <div>
                <Page
                    title="Kontakt"
                    paragraphs={paragraphs}
                    isBackButton={false}
                />
            </div>
        );
    }
}