import '../../../Css/index.css';
import React from 'react';

import SubPage from '../../../Page/PageElements/section.jsx';
import ChallengeBox from '../../../Page/ContentComponents/challengeBox.jsx';

const list = [
    <div>Narzdzie do wybijania misek sterw  np. parktool RT-1 (nie dotyczy sterw zintegrowanych)</div>,
    <div>Narzdzia do osadzania misek sterw  np. parktool HPP-2 (nie dotyczy sterw zintegrowanych)</div>,
    <div>Narzdzie do osadzania bieni na koronie widelca  np. parktool CRS</div>,
    <div>Narzdzie do usuwania bieni korony widelca np. parktool CRP-1</div>,
    <div>Zestaw kluczy szecioktnych</div>,
    <div>Smar montaowy</div>,
    <div>Szmatka</div>,
    <div>Suwmiarka</div>,
    <div>Motek z gumow gwk (opcjonalne)</div>
]

const paragraphs = [
<h1>Pena wymiana sterw:</h1>,
    <ChallengeBox
    skillLevel = "challengeBar75"
    toolLevel = "challengeBar100"
    list = {list}
    />,
    <div>Pocztek jest dokadni taki sam jak przy zwykej wymianie oysk opisanej powyej. Aby dobra si do oysk bdziesz musia docelowo wyj widelec. 
        Bd na to przygotowany, aby w trakcie demontau nie zorientowa si, e pltanina napitych kabli ci to uniemoliwi, lub w znaczcy sposb utrudni, wyduajc ca napraw. 
        Prawdopodobnie dobrym rozwizaniem jest te przygotowanie podprki, aby ram bez widelca oprze w bezpieczny i stabilny sposb. 
        Sugeruj take wyjcie koa dla uatwienia sobie manewrw wyjtymi komponentami.</div>,
    <div>
        <ul>
            <li>Poluzuj ruby boczne wspornika kierownicy (mostek),</li>
            <li>Odkr zupenie rub grn rury sterowej i wyjmij j razem z kapslem,</li>
            <li>Zdejmij wspornik kierownicy z rury sterowej</li>
            <li>Zabezpiecz kierownic w taki sposb, aby nie luno wiszc na kablach nie porysowaa ramy</li>
            <li>Zdejmij wszystkie podkadki z rury sterowej oraz grne podkadki sterw,</li>
            <li>Jeli widelec nie wysuwa si z ramy i sterw, uyj motka z gumow gwk, lub innego podobnego narzdzia aby go wybi.</li>
            <li>Zabezpiecz Widelec w taki sposb, aby zwisajc na kablach nie porysowa ramy</li>
            <li>Wyjmij oyska (s to albo wianki albo oyska maszynowe),</li>
            <li>Wybij miski sterw narzdziem parktool RT-1. 
                a.	UWAGA: Ramy ze zintegrowanym systemem sterw maj miski wbudowane w ram, wic w takim przypadku nie bdzie to konieczne.</li>
            <li>Usu bieni korony widelca narzdziem parktool CRP-1</li>
            <li>Zmierz suwmiark rednic wewntrzn ramy w miejscu osadzenia misek sterw  na grze oraz na dole. 
                Na podstawie wartoci dobierz waciwe stery z tabeli podanej na pocztku artykuu.
                    a.	Uwaga: musisz wzi pod uwag jeszcze warto widelca. Moe si zdarzy, e masz ram taperowan, a rura sterowa widelca to 1 i 1/8 na caej dugoci. 
                    W takim wypadku bdziesz potrzebowa specjalne stery, stanowice jednoczenie form przejciwki</li>
            <li>Dokadnie wyczy wewntrzn cz ramy. Na cienk warstw smaru montaowego.</li>
            <li>Osad miski nowych sterw za pomoc narzdzia parktool HPP-2.
                a.	Miska musi wchodzi w ram idealnie pod ktem prostym. Jeeli podczas osadzania si zelizgnie i straci dobry kt  nie kontynuuj. Wybij misk i zacznij od pocztku.
                b.	Najlepiej jedn misk po drugiej  unikaj usadzania obu jednoczenie. 
                c.	Osadzajc miski sterw bardzo atwo jest uszkodzi ram lub stery. Zachowaj szczegln ostrono, dziaaj powoli i bez popiechu.</li>
            <li>Dokadnie wyczy koron widelca. Na cienk warstw smaru montaowego, a nastpnie osad bieni na koronie widelca za pomoc narzdzia parktool CRS.</li>
            <li>Na warstw smaru na miski, umie w nich oyska, a nastpnie umie jeszcze minimaln ilo smaru na grnej stronie samych oysk,</li>
        </ul>
    </div>
    ]

    const FullService = () => {
        return (
            <div>
            <SubPage 
                title="Wymiana sterw"
                paragraphs={paragraphs}
            />
            </div>
        );
    }
     
    export default FullService;