import '../../../Css/index.css';
import React from 'react';
import ArticleBody from '../../../Page/PageElements/article.jsx';
import Section from '../../../Page/PageElements/section.jsx';
import PreassureInfo from '../../../../resources/wheel/tyre/preassureTyreInfoPrint.PNG';
import PompWithBarometer from '../../../../resources/wheel/tyre/pompWithBarometer.jpg';
import ValveAdapter from '../../../../resources/wheel/tyre/valveAdapter.PNG';
import ElectronicBarometer from '../../../../resources/wheel/tyre/electronicBarometer.PNG';
import Barometer from '../../../../resources/wheel/tyre/barometer.jpg';




const section1 = [
    <div>
        Jakie powinno by prawidowe cinienie w koach? Od czego wysoko cinienia zaley, w jakich jednostkach przedstawia si cinienie oraz
        jakie s optymalne wartoci dla rnych rodzajw rowerw? Tego i jeszcze wicej dowiesz si poniej.
    </div>,
    <div>
        Kada opona na bocznej cianie na podan informacj o prawidowym cinieniu, podan w co najmniej jednej z trzech jednostek miary: PSI, BAR lub rzadziej KPA.
        Przykad na oponie szosowej:
    </div>,
    <div>
        <img className="photo-horizontal" src={PreassureInfo} alt="info"></img>
    </div>,
    <div>
        Informacja ta moe by w formie maksymalnego cinienia [ max 8 BAR ], albo w formie zakresu, [ 2 - 4 BAR ] .
    </div>
]

const section2 = [
    <div>
        Do kontroli cinienia su barometry. Na rynku jest szeroki wybr pompek z wbudowanym barometrem, i to zarwno druych, pionowych, jak i rcznych.
        Praktycznie zawsze posiadaj nadrukowan skal zarwno BAR jak i PSI.
        Aby sprawdzi sinienie wystarczy naoy wlot na wentyl a wskazwka barometru od razu powinna przesun si na liczb z aktualnym wynikiem.
    </div>,
    <div>
        UWAGA: barometr bdzie wskazywa cinienie tylko, kiedy dtka jest naoona na opon i koo. Zdarza si, e ludzie, chcc sprawdzi, czy zakupiony barometr dziaa,
        pompuj dtk poza rowerem. Taka dtka bez opony, ktre blokuje jej "rozrost" bdzie jednak po prostu rosa tak dugo a pknie, a cinienie na barometrze bdzie cay czas
        pokazywao zero. Pracujc w sklepie rowerowym, przyjem bardzo wiele reklamacji tego typu ;)
    </div>,
    <div>
        <img className="photo-medium" src={PompWithBarometer} alt="pompBarometer"></img>
        <img className="photo-medium" src={Barometer} alt="barometer"></img>
    </div>,
    <div>
        Dodatkowo istniej elektroniczne barometry, ktrymi co prawda nie napompujemy koa, ale sprawdzimy aktualne cinienie i spucimy powietrze do podanej wartoci.
        S szczeglnie przydatne, jeeli nie chcemy bra ze sob w drog pompki rcznej, a wci z jakiego wzgdu chcemy kontrolowa cinienie. Szczeglnie na rowerach grskich
        zdarzaj si sytacje w ktrym wjeda si na gr z wysokim cinienie, aby na jej szczycie spuci powietrze i przez to zyska na przyczepnoci i amortyzacji podczas zjazdu.
    </div>,
    <div>
        <img className="photo-medium" src={ElectronicBarometer} alt="electronicBarometer"></img>
    </div>
]

const section3 = [
    <div>
        Jeli nie masz barometru w adnej postaci, to moesz zawsze podjecha na najblisz stacj beznynow i skorzysta z kompresora, ktry zawsze jest wyposaony w miernik cinienia. Jeeli twr rower ma wentyle typu
        Shreder, czyli samochodowe, zrobisz to bez adnego problemu. Jeli natomiast masz wentyle typu presta lub rowerowe, polecam zaopatrzy si w przejciwk.
    </div>,
    <div>
        <img className="photo-medium" src={ValveAdapter} alt="valveAdapter"></img>
    </div>,
    <div>
        Dowiadczone w temacie osoby, s w stanie z do du precyzj okreli cinienie "na palec". Natomiast kademu i w kadym jednym wypadku polecam sprawdzi cinienie przed jazd rowerem,
        szczeglnie jeeli wsiadasz na rower po pewnej przerwie. Prawidowe cinienie w koach zapewni ci atw i optymaln jazd, podczas gdy nawet najdroszy rower z niewaciwym cinieniem bdzie
        prowadzi si jak najgorszy zom, a by moe nawet stanowi zagroenie.
    </div>
]

const content = [
    <Section paragraphs={section1} />,
    <Section paragraphs={section2} title={'Skd mam wiedzie jak mocno napompowaem?'} />,
    <Section paragraphs={section3} title={'Nie mam barometru, co mi pozostaje?'} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Prawidowe cinienie w koach"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}