import '../../../Css/index.css';
import React from 'react';
import Section from '../../../Page/PageElements/section.jsx';
import ArticleBody from '../../../Page/PageElements/article.jsx';
import ContinentalWornout from '../../../../resources/wheel/tyre/continentalWornout.PNG';
import TyreLowPreassureWornout from '../../../../resources/wheel/tyre/tyreLowPreassureWornout.PNG';

const section1 = [
    <div>
        Stopie zuycia opon jest w stanie oceni kady i to w domowych warunkach, jeli tylko zna kilka podstawowych regu. Te natomiast
        mog si od siebie rni, w zalenoci od typu roweru, opon oraz nawierzchni po ktrej jedzisz. Czasami moe wydawa si, e opona jest ju do wyrzucenia, ale
        tak naprawd posuy jeszcze wiele kilometrw. Zacznijmy wic od identyfikacji typu opon, ktre masz. Przyjmijmy, e s trzy typy - [1] typowe grskie, z wyranym bienikiem i kostk,
        [2] crossowe, lub semi-slicki, ktre bienikiem mocno przypominaj opony samochodowe - gadne, ale z licznymi wciciami i "wzorkami", oraz [3] slicki, czyli zupenie gadkie opony, spotykane
        zasadniczo wycznie w rowerach szosowych.
    </div> 
]

const section2 = [
    <div>
        Opony grskie to wielki balon z wieloma kostkami, ktre zapewniaj odpowiedni trakcj na mikkim podou. Najatwiej rozpozna stopie zuycia takiej opony, obserwujc bienik
        oraz sposb w jaki zmienia si wraz z coraz to kolejnymi wyjazdami. Jeeli krawdzie kostek staj si mniej wyrane, lub s wrcz poobrywane, to znak, e ich czas jest blisko.
        Jedc na mikkich oponach po zbyt twardym podou moe wrcz si zdarzy, e zbku zupenie si pourywaj, zostawiajc nieadne fragmenty drutu.
    </div>,
    <div>
        Bardzo popularnym defektem jest jazda na koach o zbyt niskim cinieniu. Wtedy na ciankach bocznych opony mona zauway wychodzce wkna, tak jak na obrazku poniej. Nie musi
        to oczywicie oznacza, e opona jest do wyrzucenia, ale jeeli cianki s ju zbyt mocno zniszczone, taka opona moe eksplodowa.
    </div>,
    <div>
        <img className="photo-horizontal" src={TyreLowPreassureWornout} alt="tyreLowPreassureWornout"></img>
    </div>
]

const section3 = [
    <div>
        W przypadku tych opon sprawa jest dos prosta, poniewa wystarczy doglda tego, czy opona p-gadka nie staa si opon gadk ;) Jeeli wcicia, rowki oraz wzory zanikaj,
        to oznaka, e stara si ju zbyt dua warstwa gumy.
    </div>
]

const section4 = [
    <div>
        Nowa opona typu slick zawsze bdzie mie adny, okrgy profil. Zuywa si poprzez stopniowe cieranie si kolejnych warstw gumy, poczynajc od samego czubka.
        Jeeli wic profil przestaje by okrgy, a zaczyna czubek jest "city", opona jest do wymiany. Wielu producentw dodatkowo oznacza stopie zuycia opon. Poniej przykad
        z opon continental, majcych charakterystyczne dwa rowki. Jeeli rowki w pewnym momencie znikn, to znaczy, e guma stara si ju ponad norm i naley uda si do sklepu
        po nowe egzemplarze.
    </div>,
    <div>
        <img className="photo-horizontal" src={ContinentalWornout} alt="continentalWornout"></img>
    </div>
]

const content = [
    <Section paragraphs={section1} />,
    <Section paragraphs={section2} title={'Opony grskie'} />,
    <Section paragraphs={section3} title={'Opony crossowe'} />,
    <Section paragraphs={section4} title={'Opony gadkie'} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Zuycie opon"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}