import '../../../Css/index.css';
import React from 'react';
import ArticleBody from '../../../Page/PageElements/article.jsx';
import Section from '../../../Page/PageElements/section.jsx';
import ChainChecker from '../../../../resources/drivetrain/chainChecker.PNG';
import ChainCheckerInAction from '../../../../resources/drivetrain/chainCheckerInAction.PNG';
import ChainringWornout from '../../../../resources/drivetrain/chainringWornout.PNG';




const section1 = [
    <div>
        Zanim przejdziemy do waciwej czci artykuu, odpowiedzmy sobie dlaczego w ogle powinno kontrolowa si zuycie napdu i wymienia jego komponenty.
        My, ludzie, jestemy przyzwyczajeni do mylenia, e jak co dziaa aktualnie dobrze, to nie trzeba tego wymienia a do momentu, kiedy co zacznie dziaa nie tak
        jak sobie bymy tego yczyli. To jednak nie do koca tak jest. Podobnie jak z zbami, bywa, e mimo wystpujce prchnicy, aden bl nie wystpuje. Natomiast kiedy ju zacznie
        wystpowa, okazuje si, e jest ju za pno i trzeba np. zatru zb.
    </div>,
    <div>
        Dlatego stan zbw jak i napdu polecam kontrolowa regularnie, po to, aby ewentualna naprawa bya szybka i tania. Jeeli w por zauwaysz, e twj acuch jest ju zuyty,
        czeka ci wymiana jedynie tego, relatywnie taniego kompontentu, ktrego cena to ok. 80 PLN. Jeeli zignorujesz temat, zuyty acuch zacznie najpierw niszczy zby kasety,
        a nastpnie zbatki korby. Wtedy bdziesz zmuszony wymieni nie tylko acuch, ale take kaset, ktrej rozrzut cenowy to 100-400 PLN, oraz zbatki korby, czyli kolejne kilkaset zotych -
        a s to kwoty, ktre nie uwzgldniaj robocizny. <b>Zgodzisz si chyba, e 80 PLN co p roku jest lepsz opcj ni 500 raz na rok, prawda?</b>
    </div>
]

const section2 = [
    <div>
        Sprawdzenie zuycia acucha jest czynnoci szybk i niesamowicie atw. Wymaga natomiast uycia pewnego taniego narzdzia, ktrym jest przymiar do acucha.
    </div>,
    <div>
        <img className="photo-horizontal" src={ChainChecker} alt="chainChecker"></img>
    </div>,
    <div>
        Jest to niesamowicie proste i uyteczne narzdzie. Powinno mie haczyk do zaczepienia o ogniwo acucha, oraz dwa zbki wskazujce procentowe zuycie acucha. Aby go uy wystarczy
        nasadzi na acuch w sposb przedstawiony poniej:
    </div>,
    <div>
        <img className="photo-medium" src={ChainCheckerInAction} alt="chainCheckerInAction"></img>
    </div>,
    <div>
        Zaprezentowany powyej przyrzd parktool CC-3.2, ktry wskazuje 0.5% oraz 0.75% zuycia acucha. W zasadzie kady producent acuchw ma wasne wytyczne, jaki % zuycia
        jest waciwym momentem do wymiany i w razie wtpliwoci polecam sprawdzi jakiej firmy masz kompontenty i poszuka w dokumentacji. Bezpiecznie jest natomiast przyj, e
        acuchy do 10 rzdw dobrze jest zmieni przy 0.75, natomiast 11 rzdowe przy 0.5.
    </div>
]

const section3 = [
    <div>
        Niestety nie ma adnego narzdzia, ktre mierzyoby dokadny poziom zuycia kasety lub zbatek korby. Natomiast jest kilka sposb, aby chocia mniej wicej oceni, czy
        napd nadaje si do wymiany. Po pierwsze, jeeli acuch jest zuyty do mocno, to moemy z ca pewnoci zaoy, e reszta te jest w podobnym stanie. Przymiar, ktry wskazuje
        0.75% i wicej powinien ju by alarmujcym sygnaem. Dodatkowo, moemy spojrze jak acuch ukada si na zbatkach z tyu i z przodu. Jeeli nie siedzi ciasno, tylko zaznaczaj si
        wyrane przerwy, tak jak na zdjciu poniej, to znaczy, e ktry komponent umiera.
    </div>,
    <div>
        <img className="photo-medium" src={ChainringWornout} alt="ChainringWornout"></img>
    </div>,
    <div>
        Na sam koniec warto poruszy jeszcze jedn wan kwesti - jeeli wszystkie znaki na niebie, ziemi i przymiarze wskazuj na to, e masz zuyty cay napd, a nie tylko acuch, to
        nie zgrywaj Poznaniaka i nie staraj si zaoszczdzi, kupujc wycznie acuch. Nowy acuch na zuytej kasecie bdzie przeskakiwa midzy ogniwami - przy kadym wikszym obcieniu bdzie
        si po prostu lizga i zuyje si w mgnieniu oka. W takiej sytuacji lepiej jest albo kupi cay napd nowy ALBO nie wymienia go w ogle. Dlaczego? Skoro zuyty dziaa sam ze sob do dobrze, 
        to moesz ju zajecha go na amen i dopiero wtedy wymieni cao. Wymiana samego acucha, w przypadku, gdy kaseta rwnie nadaje si na mietnik jest tylko wyrzucaniem pienidzy w boto. 
    </div>
]

const content = [
    <Section paragraphs={section1} />,
    <Section paragraphs={section2} title={'acuch'} />,
    <Section paragraphs={section3} title={'Inne czci napdu'} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Kiedy acuch i napd s do wymiany"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}