import React from 'react';
import '../../Css/index.css';
import Page from '../../Page/PageElements/article.jsx';
import SubPage from '../../Page/PageElements/section';

const content = [
    <div>
        Bike Workshop to strona w caoci powicona rowerom.
        Znajdziesz tutaj kompletne informacje dotyczce serwisu, konserwacji i kompatybilnoci.
        Wszystkie informacje i poradniki s przeznaczone zarowno dla cakowitych amatorw jak i profesjonalnych mechanikw.
    </div>,
    <div>
        Wszystkie materiay s cakowicie darmowe i dostpne bez adnych limitw. 
        S efektem pasji do rowerw i wielu lat spdzonych w wielu profesjonalnych serwisach rowerowych.
    </div>,
    <div>
        Jeli masz jakiekolwiek wtpliwoci lub uwagi dotyczce treci poradnikw, skontaktuj si z nami poprzez dzia O NAS - na pewno odpiszemy.
    </div>
]

const page = [
    <SubPage
        paragraphs={content}
    />,
]



const Main = () => {
    return (
        <div>
        <Page 
            title="Siema pedalarzu!"
            paragraphs={page}
            isBackButton={false}
        />
        </div>
    );
}


export default Main;