import '../../../Css/index.css';
import React from 'react';
import ArticleBody from '../../../Page/PageElements/article.jsx';

import ContentTile from '../../../Page/ContentComponents/contentTile';

import ImageEC from '../../../../resources/headset/EC.jpg';
import ImageIS from '../../../../resources/headset/IS.jpg';
import ImageZS from '../../../../resources/headset/ZS.jpg';
import ImageThread from '../../../../resources/headset/Thread.jpg';

import TableHeadsetStandards from '../Component/table-headset-standard';
import TableHeadsetTypes from '../Component/table-headset-type';

const paragraphs = [
    <div>Ze sterami rowerowymi jest tak jak ze zdrowiem - tyle o tym wiemy, ile nas boli. Pki kierownica chodzi bez adnych zgrzytw, a widelec nie ma adnego luzu,
      nawet do gowy nikomu nie przyjdzie eby je rozrkci i przeserwisowa. Rzadko, ale czasem jednak zdarzy si, e co pjdzie nie tak i trzeba je wymieni. Wtedy
      natomiast warto wiedzie jaki dokadnie typ jest w naszej maszynie. Poniej kompendium wiedzy, ktre z pewnoci pomoe ci w krytycznej sytuacji. Dodatkowo polecam artyku
      o praktycznym zastosowaniu informacji znalezionych tutaj, czyli <a href="/service/headset/1">kompletny serwis sterw.</a>
    </div>,
    <TableHeadsetTypes/>,
    <TableHeadsetStandards/>, 
    <ContentTile
    imageSource = {ImageThread}
    name = "Threaded"
    content = {<div>
      Ewolucyjnie najstarszy system z tutaj prezentowanych. 
      Wci dostpny na rynku w wielu rowerach - najczciej miejskich i trekkingowych.
      Praktycznie zawsze wystpuje w standardzie dla widelcw 1" - wyjtkiem bd jedynie rowery grskie z lat dziewidziesitych, 
      ktrym montowane gwintowane widelec 1 i 1/8 cala.

      </div>}
    />,
    <ContentTile
    imageSource = {ImageEC}
    name = "EC - external cup"
    content = {<div>
      Kolejne stadium rozwoju sterw. Najczciej wystpujca nazwa dla tego typu to A-HEAD.
      Standardem jest stosowanie razem z widelcami 1 i 1/8 cala.
      Wci dostpny na rynku w wielu rowerach - najczciej miejskich i trekkingowych.
      
      </div>}
    />,
    <ContentTile
    imageSource = {ImageZS}
    name = "ZS - zero stack"
    content = {<div>
      Inaczej stery pzintegrowane. 
      Mocowanie misek sterw, a wic rnie i caa gwka ramy s znacznie szersze ni w przypadku EC.
      Same oyska te, mimo, e wci osadzone we wciskanych zewntrznych miskach, to nie znajduj si ju na zewntrz samej ramy, ale wewntrz niej.
      Dziki temu osignito wiksz sztywno caj konstrukcji i poprawiono szczelno.
      Przy tym typie sterw te oprcz standardowych widelcw 1 i 1/8 mona te spotka widelce taperowane, czyli poszerzane w koronie do 1 i 1/2.
      [Artyku o widelcach taperowanych]
      
      </div>}
    />,
    <ContentTile
    imageSource = {ImageIS}
    name = "IS - integrated headset"
    content = {<div>
      Czyli stery zintegrowane. Konstrukcyjnie z jednej strony bardzo zblione do pzintegrowanych - posiadaj najczciej te same wymiary oysk.
      Jest jednak jedna zasadnicza rnica midzy tymi dwoma typami - zaintegrowane maj miski wbudowane w ram. Oznacza to, e przy wymianie sterw, w zasadzie jedyne, co
      podmieniamy to oyska. Nie trzeba wic bawi si z wciskarkami misek. Minusem takiego rozwizania jest "jednorazowy" charakter takich misek - jeli z jakiego powodu
      si zami, trzeba wymienia ca ram. Boli...
      Najczciej wystpuj w ramach karbonowych i wysokich modelach alu.
    
      </div>}
    />,
    
    
    <div></div>,
]

export default class Article extends React.Component {
  render() {
      return (
          <div>
              <ArticleBody
                  title="Stery - rodzaje i charakterystyka"
                  paragraphs={paragraphs}
                  backButtonLink='/compatibility'
              />
          </div>
      );
  }
}