import '../../../Css/index.css';
import React from 'react';
import Section from '../../../Page/PageElements/section.jsx';
import ArticleBody from '../../../Page/PageElements/article.jsx';

const section1 = [
    <div>
        No jak to jak? <s> Bierzemy oliwk, nanosimy grub warstw na acuch, a dla bezpieczestwa rwnie i kaset i zbatki korby. Czym grubsza warstwa pynu, tym lepiej! </s> Ot nie.
        Zbyt dua ilo oliwki do acucha szkodzi tak samo jak jej brak. Po prawidowym naoeniu powinna znajdowa si wewntrz ogniew, z zewntrz natomiast acuch powinien by
        stosunkowo suchy. Jak to osign? Polecam nanie jedn kropl oliwki na kade ogniwo po kolei (zajmie ci to ok. 2 minuty) a nastpnie odczeka minimum godzin. Najlepiej jednak
        zrobisz, jeli zostawisz rower na noc w spokoju. Nastpnie nad ranem zatrzyj nadmiar oliwki szmatk. Tak przesmarowany napd bdzie chodzi cicho, nie bdc jednoczenie lepem
        na brud.
    </div>,
    <div>
        Co ile nakadamy now warstw oliwki? Ok. 100 - 200 km. w zalenoci od marki i modelu. 
    </div>
]


const content = [
    <Section paragraphs={section1} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Smarowanie acucha"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}