import '../../../Css/index.css';
import React from 'react';
import ArticleBody from '../../../Page/PageElements/article.jsx';

import ContentTile from '../../../Page/ContentComponents/contentTile';

import ImageSquare from '../../../../resources/bottomBracket/square.jpg';
import ImageOctalink from '../../../../resources/bottomBracket/octalink.png';
import ImageIntegratedThreaded from '../../../../resources/bottomBracket/integratedThreaded.jpg';
import ImagePressFit from '../../../../resources/bottomBracket/pressFit.jpg';

import TableThreadedBottomBracketDimensions from '../Component/table-bb-threaded-dimensions';
import TablePFdimensions from '../Component/table-bb-pf-dimensions';



const paragraphs = [
    <div>
    Suport jest jedn z tych czci roweru do ktrych przywizuje si wyjtkowo mao uwagi i to zarwno na etapie sprzeday sklepowej jak i na etapie 
    uytkowania. Z suportem jest troch jak ze zdrowiem - doceniamy, jego bezproblemowe dziaanie dopiero w momencie kiedy zaczyna dziaa le. Jest to element
    kluczowy w aspekcie sztywnoci napdu a jego kolejne postacie na drodze rozwoju potrafiy prowadzi w rewolucji w aspekcie konstruowania ram.
    </div>,
    <TableThreadedBottomBracketDimensions/>,
    <TablePFdimensions/>,
    <ContentTile
    imageSource = {ImageSquare}
    name = "Kwadrat"
    content = {<div>
      Najpopularniejszy standard w rowerach do 4000 PLN. Wysoka waga sigajca ok. 300 gramw. Jest te mao uniwersalny, dlatego
      e nawet w obrbie korb tej samej firmy, kady model wymaga specjalnej dugoci osi. Dodatkowo jest to najmniej sztywny system
      z tutaj prezentowanych. Mimo wszystkich tych wad jest to bez wtpienia niezastpiony system w rowerach budetowych z uwagi na 
      bardzo nisk cen oraz nisk awaryjno. 
      Wicej informacji. 
      Jak dobra korb na kwadrat.
    </div>}/>,
    <ContentTile
      imageSource = {ImageOctalink}
      name = "Octalink"
      content = {<div>
    Typ bardzo zbliony do kwadratu. Rnica polega w zasadzie wycznie na ksztacie osi, co samo przez si oznacza wymg stosowania innego systemu korbowego.
    Zamiast osi w kstacie kwadratu mamy do czynienia z wakiem o nieco wikszej rednicy z omioma wypustkami. Oznacza to, e korba ma oparcie na wikszej iloci punktw,
    a wic sztywno caej konstrukcji nieco wzrasta. Czy ten zysk uzasadnia jednak wprowadzenie nowego standardu o wyszej cenie? W swoim czasie, kiedy bya to jedyna alternatywa - by moe.
    Aktualnie jednak, kiedy na rynku system zintegrowane zadomowiy si na dobre, octalink nie ma adnej racji bytu, a utrzymywanie dalej tego standardu powoduje wycznie wicej komplikacji.
    Wicej informacji. 
    Jak dobra korb na kwadrat.
    </div>}/>,
    <ContentTile
        imageSource = {ImageIntegratedThreaded}
        name = "Suport gwintowany zintegrowany"
        content = {<div>
          Po wielu latach prb zredefiniowania suportu i przemodelowywania systemw kartridowych, zrobiono potrzebny krok w ty, aby dostrzec co byo gwnym czynnikiem blokujcym rozwj
          brany. W tym systemie, punkt oparcia zosta przesunity na zewntrz ramy, co pozwolio na zastosowanie wikszych ni dotychczas oysk, a co za tym idzie take osi o wikszej rednicy.
          Dodatkowo punkt oparcia by szerzej rozstawiony ni dotychczas, co zaowocowao drastycznym skokiem sztywnoci napdu. Dodatkowo o o tak duej rednicy nie potrzebowaa a tak grubej warstwy
          metalu, czym zaoszczdzono sporo gramw. Jakie s zatem wady? Kilkukrotnie wysza cena ni w przypadku kwadratu oraz wiksza awaryjno. 
    </div>}/>,
    <ContentTile
          imageSource = {ImagePressFit}
          name = "Press fit"
          content = {<div>
           Poprzednie rozwizanie byo zredefiniowaniem wkadu suportu, natomiast press-fit jest zredefiniowaniem mufy suportu. Filozofia jest podobna co w systemach gwintowanych - wiksze oyska, szerzej
           rozstawione anieli w typach kartridowych. Tak jak systemy gwintowane mona stosowa bez najmniejszego problemu w ramach przystosowanych do kartridy, tak suporty typu press-fit
           s kompatybilne wycznie w ramach o mufie bezgwintowej. Miski wciska si w ram za pomoc specjalnej prasy. Rnica jest zatem zarwno we wkadzie suportu (ktry jest najczciej plastikowyj dla niszej wagi) jak i mufie
           , ktra w tym systemie jest szersza i ma wiksz rednic otworu. Rozwizanie jeszcze bardziej zwiksza sztywno, jednak odbywa si to kosztem atwoci uytkowania - prasa do wymiany
           tego typu wkadw nie naley do najtaszych i najpowszechniej dostpnych, co niemal z ca pewnoci wyklucza zabieg wymiany w domu i skazuje uytkownika takiego roweru na regularne wizyty w 
           serwisie.
    </div>}/>
]

export default class Article extends React.Component {
  render() {
      return (
          <div>
              <ArticleBody
                  title="Wkad suportu"
                  paragraphs={paragraphs}
                  backButtonLink='/compatibility'
              />
          </div>
      );
  }
}