import React from 'react';
import '../../Css/index.css';
import Page from '../../Page/PageElements/article.jsx';
import CompatibilityMenu from './menu';

const paragraphs = [
    <div>Sekcja zwizana z zagadnieniami istniejcych na rynku standardw rowerowych, dostpnych czci oraz moliwych kompromisw i przyjciwek.</div>,
    <CompatibilityMenu />
]

export default class Compatibility extends React.Component {
    render() {
        return (
            <div>
                <Page
                    title="Kompendium wiedzy o czciach rowerowych"
                    paragraphs={paragraphs}
                    isBackButton={false}
                />
            </div>
        );
    }
}