import '../../Css/index.css';
import React from 'react';

import Page from '../../Page/PageElements/article.jsx';
import ProductsMenu from './menu';

const paragraphs = [
    <div>
        W tej sekcji znajdziesz recenzje wszelkiego typu produktw rowerowych i okoorowerowych. Wszystko jest kwesti gustu i podejcia, ale niektre
        czci bd marki s wyjtkowo lubiane i polecane przez nas. Trafi wtedy do kategorii polecane.

    </div>,
    <ProductsMenu />
]

export default class Compatibility extends React.Component {
    render() {
        return (
            <div>
                <Page
                    title="Recenzje i opinie o produktach rowerowych"
                    paragraphs={paragraphs}
                    isBackButton={false}
                />
            </div>
        );
    }
}