import '../../../Css/index.css';
import React from 'react';
import Section from '../../../Page/PageElements/section.jsx';
import WarningBox from '../../../Page/ContentComponents/warning.jsx';
import ArticleBody from '../../../Page/PageElements/article.jsx';

const section1 = [
    <div>
        Jazda rowerem jest bardzo przyjemna, ale prdzej czy pniej staniesz przed perspektyw jazdy na zapuszczonym brudasie, do ktrego
        poza wielk mioci, bdziesz odczuwa take wstrt. Rowery niestety rwnie trzeba my, ale mao kto wie jak zrobi to dobrze w warunkach
        domowych, aby w aden sposb go nie uszkodzi. Zdarza si przecie, e niejeden domorsy mechanik po urzdzeniu takiego domowego SPA, po tygodniu
        zachodzi w gow dlaczego wszystko tak haasuje, ma luzy i dziaa duo gorzej ni wczeniej.
    </div>,
    <WarningBox
    warningText="UWAGA: artyku ten skupia si na myciu roweru jako caoci - jeeli chcesz wiedzie jak prawidowo umy koa lub napd, zapraszam do przeznaczonych tym
    zagadnieniom odrbnym artykuw."
    />
]

const section2 = [
    <div>
        Najbezpieczniejszy i zarazem najbardziej czasochonny sposb. Polecam szmatk oraz atomizer z wod i dowoln chemi do czyszczenia. Ram i koa w wikszoci
        wyczycisz w ten sposb bez najmniejszego problemu, ale do wielu miejsc po prostu nie dojdziesz i skoczy si na tym, e bdziesz bezskutecznie prbowa doj palcem
         trudno dostpne spawy w okolicach suportu. O dokadnym czyszczeniu hamulcw lub piast te moesz zapomnie.
    </div>
]

const section3 = [
    <div>
        Najefektywniejszy sposb czyszczenia rowerem, ale jednoczenie bardzo ryzykowny, jeeli nie wiesz jak to zrobi prawidowo. Myjk cinieniow bardzo atwo jest
        wypuka smar z suportu, piast oraz sterw, a przez to narazi rower na liczne defekty i niezbyt atwe naprawy. Obowizkowo wic omijaj wymienione miejsca, a wszystkie
        inne pryskaj, zachowujc minimum jeden metr odlegoci. Zanim jednak w ogle zaczniesz pryska wod, polecam znale dobry rodek czyszczcy, usuwajcy smar i tuszcz,
        spryska nim cay rower a nastpnie odczeka ok. dwie minuty. Na sam koniec koniecznie wysusz rower pistoletem ze spronym powietrzem. Zakadam, e jeeli uywasz
        myjki cinieniowej, to do tego tak bdziesz mie dojcie. 
    </div>
]

const content = [
    <Section paragraphs={section1} />,
    <Section paragraphs={section2} title={'Mycie rczne'} />,
    <Section paragraphs={section3} title={'Myjka cinieniowa'} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Jak prawidowo my rower"
                    secondaryTitle="eby go nie uszkodzi"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}