import '../../../Css/index.css';
import React from 'react';
import Section from '../../../Page/PageElements/section.jsx';
import ArticleBody from '../../../Page/PageElements/article.jsx';
import ChainCleaningMachine from '../../../../resources/drivetrain/chainCleaningMachine.PNG';
import Fenwicks from '../../../../resources/drivetrain/fenwicks.PNG';



const section1 = [
    <div>
        Napd to zdecydowanie jedna z najtrudniejszych do wyczyszczenia czci rowerw. Jest to zajcie niewdziczne, czasochonne, i wielokrotnie po prostu niewarte zachodu.
        Zawsze miej na uwadze, e jeeli zabierzesz si do tego nieprawidowo i zaczniesz uywa byle jakich specyfikw chemicznych, moesz wyrzdzi swojemu rowerowi krzywd.
    </div>
]

const section2 = [
    <div>
        Bardzo popularnym sposobem czyszczenia acucha, stosowanym wielu amatorw, oraz, o zgrozo, warsztaty rowerowe, jest mycie napdu w soiku z benzyn ekstrakcyjn, co 
        <b>zdecydowanie odradzam</b>. Benzyna tak zastosowana zostawia brzydki nalot, czyci sabo oraz wysusza acuch i powoduje luz na ogniwach. Co innego w przypadku nanoszenia
        pewnych iloci [1] beznyny na szmatk i przeszorowaniu wszystkiego rcznie. Wci nie jest to super efektywny sposb, ale co nieco w ten sposb da si wyczyci. W zasadzie
        niewiele wicej da si powiedzie o wszelkiej maci [2] odtuszczaczasz - s okej, ale istnieje sporo lepszych sposobw.
    </div>,
    <div>
        Na rynku mamy te [3] maszynki czyszczce. Ich plusem jest na pewno tego, e nie trzeba zdejmowa acucha - mona to robi na rowerze tak jak stoi. Musz powiedzie, e
        przy zastosowaniu z rop, albo dobrym rodkiem czyszczcym co takiego sprawuje si naprawd niele. 
    </div>,
    <div>
        <img className="photo-medium" src={ChainCleaningMachine} alt="chainCleaningMachine"></img>
    </div>,
    <div>
        Na sam koniec zostawiem mj ulubiony sposb czyszczenia, czyli [4] pianka marki Fenwicks. Nie syfi wok, bardzo adnie pachnie i czyci wszelkie zabrudzenia i smary tak dobrze, 
        e uywam jej z powodzeniem do wszystkich czci napdu, wielu partii roweru, a take do czyszczenia wielu zabrudze nawet nie zwizanych z rowerem. Piank wystarczy naoy
        na acuch, odczeka kilka minut i zacz ciera. Naprawd polecam.
    </div>,
    <div>
        <img className="photo-medium" src={Fenwicks} alt="fenwicks"></img>
    </div>
]

const section3 = [
    <div>
        Koniec tego artykuu powinien by jednoczenie twoim punktem wyjcia w myleniu o tym jak wyczyci napd. Kady brud jaki zbiera si na twoim napdzie jest niczym innym
        jak efektem dugoterminowego zaniedbania lub/oraz nieprawidowo przesmarowanego acucha. Jest absolutn norm to, e przecitny rowerzysta, jeeli zaczyna smarowa rower,
        to nakada hektolitry oliwki, i niejednokrotnie nie tylko na acuch, ale i kaset i korb. Do tak zmagazynowanej, lepkiej cieczy przyklei si z atwoci kada jedna
        drobinka pyu jak napotkasz na drodze. Prawdopodobnie wic zamiast powica godziny na czyszczeniu roweru, powiniene skupi si na tym, aby acuch by przesmarowany
        prawidowo.
    </div>
]

const content = [
    <Section paragraphs={section1} />,
    <Section paragraphs={section2} title={'Czym i jak czyci?'} />,
    <Section paragraphs={section3} title={'Lepiej zapobiega ni leczy'} />
]

export default class Article extends React.Component {
    render() {
        return (
            <div>
                <ArticleBody
                    title="Jak prawidowo czyci napd"
                    paragraphs={content}
                    backButtonLink='/maintenance'
                />
            </div>
        );
    }
}